import React from 'react';
import photo from '../assets/images/Foto.jpg';
import './presentation.css';

const Presentation = () => {
    return (
        <div className='presentationGrid'>
            <p className='presentationText'>Hola! Mi nombre es Maitn. <br></br>
                Soy estudiante de Ingeniera en Sistemas de Informacin.<br></br>
                Actualmente vivo en Concepcin del Uruguay, Entre Ros, Argentina.</p>
            <img src={photo} alt='Foto de Maitn Blanc' className='photo' />
            <a className='cv' href='../assets/CV.pdf' target='blank'>
                <i class="bi bi-file-earmark-person"></i> Visualizar CV aqu</a>
        </div>
    );
}

export default Presentation;
