import React from 'react';
import './aboutMe.css';

const AboutMe = () => {
    return (
        <div className='aboutMe' id='aboutMe'>
            <h2 className='aboutMe-title'>Sobre M:</h2>
            <p className='aboutMe-text'>
                &emsp; Tengo gran inters en el Desarrollo Web y la programacin orientada a objetos. <br></br>
                &emsp; Poseo gran capacidad para adaptarme a todo tipo de entornos y aportar siempre lo mejor de m. Me
                caracterizo por mi facilidad para el trabajo en equipo y mi entusiasmo por aprender y desarrollar
                mis habilidades.<br></br>
                &emsp; Estoy en busca de una oportunidad laboral en la que adquirir ms experiencia, mientras contino
                con mis estudios con el fin de crecer personal y profesionalmente.<br></br>
            </p>

        </div>
    );
}

export default AboutMe;
